body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.hideMe {
    display: none;
}

.signupSpacer {
    width: 80%;
    margin: auto;
}

.signupBody {
    background-color: lightblue;
    min-height: 20vh;
    font-size: calc(5px + 2vmin);
    color: black;
    padding: 15vh;
    margin: 5vh;
    line-height: 5vh;
  }

P.alreadyMember {
 display: inline-block;
 margin: auto;
 padding-left: 20vh;
 font-size: 25px;
  }

input.form-control {
      margin-left: 15px;
  }
.App-footer {
text-align: center;
font-size: 25px;
min-height: 10vh;
margin-top: 45px;
position: -webkit-sticky;
position: sticky;
border: 2px;
border-color: black;
background-color: green;
line-height: 80px;
}
  ul.App-navbar {
    border-color: rgb(255, 169, 169);
    border-width: 2px;
    border-style: inset;
    position: -webkit-sticky;
    position: sticky;
    padding: 10px;
    background-color: #282c34;
    height: 10vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    font-size: calc(12px + 2vmin);
    color: white;
    text-transform: uppercase;
  }

  li.menuLink {
    list-style-type: none;
    margin-left: 5px;
    margin-right: 5px;
    padding: 20px;
    font-size: calc(8px + 2vmin);
  }

  img.logo {
    width: 50px;
  }
  
  A:hover.logo { text-decoration: none; }

  A.menuLink { color: white }

  A:link.menuLink { text-decoration: none; padding: 15px;}

  A:active.menuLink { text-decoration: none; }

  A:hover.menuLink { color: black; text-decoration: none; background-color: rgb(197, 197, 197); border-radius: calc(8px + 2vmin); border-color: black; border-width: 1px; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s ease-in-out;
          animation: App-logo-spin infinite 10s ease-in-out;
  height: 60px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-body {
height: 80vh;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

